
import { fileSizeFormater } from "../../components/file-item/utils";
import { LocalLabels } from "../localization";

/**
 * English translation for Dropzone component
 */
export const DropzoneTraditionalChinese: LocalLabels = {
    defaultLabel: " ",
    uploadingMessage: (amountOfFiles) => { return `${amountOfFiles}`; },
    uploadFinished: (uploaded, rejected) => `: ${uploaded}, ${rejected}`,
    noFilesMessage: ``,
    footer: {
        acceptAll: ``,
        acceptCustom: (accept) => `${accept}`
    },
    header: {
        uploadFilesMessage: "",
        maxSizeMessage: (maxFileSize) => `${maxFileSize}`,
        validFilesMessage: (numberOfValidFiles, maxFiles) => `  ${numberOfValidFiles}/${maxFiles}`
    },
    fakeuploadsuccess: "",
    fakeUploadError: "",
}

/**
 * English translation for FileItem component
 */
export const FileItemTraditionalChinese: LocalLabels = {
    fullInfoLayer: {
        name: `: `,
        size: ": ",
        type: ": "
    },
    status: {
        uploading: "",
        success: "",
        valid: "",
        denied: "",
        error: ""
    },
}

/**
 * English translation for Validation Errors
 */
export const ValidateErrorTraditionalChinese: LocalLabels = {
    maxSizeError: (maxSize) =>
        `  ${fileSizeFormater(maxSize as number)}`,
    acceptError: ``,
    maxFileCount: (maxFiles) =>
        ` (${maxFiles})`
}