import { fileSizeFormater } from "../../components/file-item/utils";
import { LocalLabels } from "../localization";

/**
 * Russian translation for Dropzone component
 */
export const DropzoneRussian: LocalLabels = {
    defaultLabel: "   .",
    uploadingMessage: (amountOfFiles) => { return ` ${amountOfFiles} `; },
    uploadFinished: (uploaded, rejected) => ` : ${uploaded},  : ${rejected}`,
    noFilesMessage: `     `,
    footer: {
        acceptAll: `    `,
        acceptCustom: (accept) => ` : ${accept}`
    },
    header: {
        uploadFilesMessage: "",
        maxSizeMessage: (maxFileSize) => ` : ${maxFileSize}`,
        validFilesMessage: (numberOfValidFiles, maxFiles) => ` ${numberOfValidFiles}/${maxFiles}`
    },
    fakeuploadsuccess: "   ",
    fakeUploadError: "  ",
}

/**
 * Russian translation for FileItem component
 *
 */
export const FileItemRussian: LocalLabels = {
    fullInfoLayer: {
        name: `: `,
        size: ": ",
        type: "T: "
    },
    status: {
        uploading: "",
        success: "",
        valid: "",
        denied: "",
        error: ""
    },
}

/**
 * Russian translation for Validation Errors
 */
export const ValidateErrorRussian: LocalLabels = {
    maxSizeError: (maxSize) =>
        `  .     - ${fileSizeFormater(maxSize as number)}`,
    acceptError: `   `,
    maxFileCount: (maxFiles) =>
        `    (${maxFiles})`
}